import axios from "axios";
import { createContext, useState} from "react";

export let CartContext=createContext()
export function CartContextProvider({children}){
    let [numbrsItems,setNumbrsItems]=useState(0)
    let [cartList,setCartList]=useState([])

    function getItemFromCart(){
        return axios.get('https://ecommerce.routemisr.com/api/v1/cart',{
            headers:{
                token:localStorage.getItem('user')
            }
            })
    }
    function addToCart (productId){

        let bodyCart={
            productId:productId
        }
    
        return axios.post('https://ecommerce.routemisr.com/api/v1/cart',bodyCart,
            {
            headers:{
                token:localStorage.getItem('user')
            }
            })
        
    }
    function deleteItemFromCart(productId){
        return axios.delete(`https://ecommerce.routemisr.com/api/v1/cart/${productId}`,
            {
            headers:{
                token:localStorage.getItem('user')
            }
            })
    }
    function updateCartQuantity(id,count){
        let body={
            count:count
        }
        
        return axios.put(`https://ecommerce.routemisr.com/api/v1/cart/${id}`,body,
            {
                headers:{
                    token:localStorage.getItem('user')
                }
            }
        )
    }

    function checkOutPayment(id,data){
        let body={
                shippingAddress:data
        }
        return axios.post(`https://ecommerce.routemisr.com/api/v1/orders/checkout-session/${id}?url=http://localhost:3000`,body,{
            headers:{
                token:localStorage.getItem('user')
            }
        })
    }

    return <CartContext.Provider value={{cartList,setCartList,addToCart,numbrsItems,setNumbrsItems,getItemFromCart,deleteItemFromCart,updateCartQuantity,checkOutPayment}} >
        {children}
    </CartContext.Provider>
}