import React, {useState } from 'react'
import { useFormik } from 'formik'
import * as yup  from 'yup'
import axios from 'axios'
import styleRegister from './Register.module.css'
import { useNavigate } from 'react-router-dom'
export default function Register() {

  let navg=useNavigate()
  let [err,setError]=useState("")
  let[loading,setLoading]=useState(true)
  // on submit
  // obj in value replace formRegister.values
  async function registerApi(value){
    setLoading(false)
    let reqRegister=await axios.post("https://ecommerce.routemisr.com/api/v1/auth/signup",value)
    .catch((err)=>
      {
        setError(err.response.data.message)
        setLoading(true)
      })
      console.log(reqRegister)
      if(reqRegister.status === 201){
        setLoading(true)
        navg('/Login')
      }
  }

  // validation input
  let validate=yup.object({
    name:yup.string().required("please Enter Your username....").min(3,"Enter at least 3 characters....").max(20,"Maximum 20 character...."),
    email:yup.string().required("please Enter Your Email....").email("Enter Valid Email...."),
    phone:yup.string().matches(/^01[0152][0-9]{8}$/,"please Enter valid phone....").required("please Enter Your Phone...."),
    password:yup.string().matches(/^[A-Z][a-zA-Z0-9!@#$%^-_]{6,16}$/,"please Enter valid Password & Start Uppercase Character....").required("Password Required & Start Uppercase Character........"),
    rePassword:yup.string().required("Password Required....").oneOf([yup.ref('password')],"Confirm Password Not Matched"),
  })

  // lib form
  let formRegister= useFormik({
    initialValues:{
      name:"",
      email:"",
      phone:"",
      password:"",
      rePassword:"",
    },
    onSubmit:registerApi,
    validationSchema:validate,
  })

  return<>
  <div className="container">
    <h3 className='mt-5'>Register Now....</h3>
    {err !== ""?<div className="alert alert-danger" role="alert">{err}</div>:''}

    <form onSubmit={formRegister.handleSubmit}>
      <div className='mb-2 mt-2'>
        <label htmlFor="name">Username</label>
        <input onBlur={formRegister.handleBlur} onChange={formRegister.handleChange} value={formRegister.values.name} className='form-control mt-2 mb-2' type="text" name="name" id="name" placeholder='Enter Username.....' />  
        {formRegister.touched.name&&formRegister.errors.name?<div className="alert alert-danger" role="alert">{formRegister.errors.name}</div>:''}
      </div>
      <div className='mb-2 mt-2'>
        <label htmlFor="email">E-Mail</label>
        <input onBlur={formRegister.handleBlur} onChange={formRegister.handleChange} value={formRegister.values.email} className='form-control mt-2 mb-2' type="email" name="email" id="email" placeholder='Enter Email....' />  
        {formRegister.touched.email&&formRegister.errors.email?<div className="alert alert-danger" role="alert">{formRegister.errors.email}</div>:''}
      </div>
      <div className='mb-2 mt-2'>
        <label htmlFor="phone">Phone</label>
        <input onBlur={formRegister.handleBlur} onChange={formRegister.handleChange} value={formRegister.values.phone} className='form-control mt-2 mb-2' type="tel" name="phone" id="phone" placeholder='Enter phone.....' />  
        {formRegister.touched.phone&&formRegister.errors.phone?<div className="alert alert-danger" role="alert">{formRegister.errors.phone}</div>:''}
    </div>
      <div className='mb-2 mt-2'>
        <label htmlFor="password">Password</label>
        <input  onBlur={formRegister.handleBlur} onChange={formRegister.handleChange} value={formRegister.values.password} className='form-control mt-2 mb-2' type="password" name="password" id="password" placeholder='Enter Password.....' />  
        {formRegister.touched.password&&formRegister.errors.password?<div className="alert alert-danger" role="alert">{formRegister.errors.password}</div>:''}
      </div>
      <div className='mb-2 mt-2'>
        <label htmlFor="rePassword">RePassword</label>
        <input  onBlur={formRegister.handleBlur} onChange={formRegister.handleChange} value={formRegister.values.rePassword} className='form-control mt-2 mb-2' type="password" name="rePassword" id="rePassword" placeholder='Enter RePassword.....' />  
        {formRegister.touched.rePassword&&formRegister.errors.rePassword?<div className="alert alert-danger" role="alert">{formRegister.errors.rePassword}</div>:''}
      </div>
      {loading?<button onClick={formRegister.handleSubmit } type="submit" className='btn btnColorBG' disabled={!(formRegister.isValid && formRegister.dirty)}>Register</button>:<button className={styleRegister.btnColorBG + ' btn text-white'}><i className='fa-solid fa-spinner fa-spin'></i></button>}
      
      
    </form>
  </div>
  </>
}
