import React, {useState } from 'react'
import { useFormik } from 'formik'
import * as yup  from 'yup'
import axios from 'axios'
import styleResetPassword from './ResetPassword.module.css'
import {useNavigate } from 'react-router-dom'


export default function ResetPassword() {
    let navg=useNavigate()
    let [err,setError]=useState("")
    let[loading,setLoading]=useState(true)
    // on submit
    // obj in value replace formReset.values
    async function resetApi(value){
        setLoading(false)
        let reqResetNewPassWord=await axios.put('https://ecommerce.routemisr.com/api/v1/auth/resetPassword',value)
        .catch((erro)=>
        {
            setError(erro.response.data.message)
            setLoading(true)
        })
        if(reqResetNewPassWord.data.token){
            navg('/login')
        }
    }

    // validation input
    let validate=yup.object({
        email:yup.string().required("please Enter Your Email....").email("Enter Valid Email...."),
        newPassword:yup.string().matches(/^[A-Z][a-zA-Z0-9!@#$%^-_]{6,16}$/,"please Enter valid New Password & Start Uppercase Character....").required("New Password Required & Start Uppercase Character........"),
    })


    // lib form
    let formReset= useFormik({
        initialValues:{
            email:"",
            newPassword:"",
        },
        onSubmit:resetApi,
        validationSchema:validate,
    })




    return<>
    <div className="container">
        <h3 className='mt-5'>Enter New Password....</h3>
        {err !== ""?<div className="alert alert-danger" role="alert">{err}</div>:''}

        <form onSubmit={formReset.handleSubmit}>
        <div className='mb-2 mt-2'>
            <label htmlFor="email">E-Mail</label>
            <input onBlur={formReset.handleBlur} onChange={formReset.handleChange} value={formReset.values.email} className='form-control mt-2 mb-2' type="email" name="email" id="email" placeholder='Enter Email....' />  
            {formReset.touched.email&&formReset.errors.email?<div className="alert alert-danger" role="alert">{formReset.errors.email}</div>:''}
        </div>
        <div className='mb-2 mt-2'>
            <label htmlFor="password">New Password</label>
            <input  onBlur={formReset.handleBlur} onChange={formReset.handleChange} value={formReset.values.password} className='form-control mt-2 mb-2' type="password" name="newPassword" id="password" placeholder='Enter Password.....' />  
            {formReset.touched.newPassword&&formReset.errors.newPassword?<div className="alert alert-danger" role="alert">{formReset.errors.newPassword}</div>:''}
        </div>
        {loading?<button onClick={formReset.handleSubmit } type="submit" className='btn btnColorBG' disabled={!(formReset.isValid && formReset.dirty)}>Login</button>:<button type='button' className={styleResetPassword.btnColorBG + ' btn text-white'}><i className='fa-solid fa-spinner fa-spin'></i></button>}
        </form>
    </div>
    </>
}
