
import axios from 'axios'
import React, { useEffect, useState } from 'react'
import Spinner from '../Spinner/Spinner'
import { Helmet } from 'react-helmet'



export default function Category() {
    let [categoryList,setCategoryList]=useState([])
    let [loading,setLoading]=useState(true)
    async function getAllCategory(){
        setLoading(true)
        let req=await axios.get('https://ecommerce.routemisr.com/api/v1/categories').catch((err)=>{
            console.log(err)
            setLoading(false)
        })
        if(req.status===200){
            setCategoryList(req.data.data)
            setLoading(false)
        }
    }
    useEffect(()=>{
        getAllCategory()
    },[])
    
    return<>
    <Helmet>
        <meta charSet="utf-8" />
        <meta name="description" content="Brands Lg Canon Dell Lenovo soney men woman Music books" />
        <title>Categories</title>
    </Helmet>
    {loading?<Spinner/>:<div className="container py-5">
                                <div className="row g-4">
                                {
                                    categoryList.map((element)=>{
                                        return <div className="col-md-2 col-6">
                                                    <div className="itemCategory border-1 border-bg-secondary-subtle border ">
                                                                <img src={element.image} alt="" height={200} className='w-100'  />
                                                                <h5 className='p-2 m-0 text-center text-main bg-white fs-6'>{element.name}</h5>
                                                    </div>
                                                </div>
                                    })
                                }
                                    
                                </div>
                            </div>
                            }
    
    
    
    
    </>
    
}
