import React, { useContext, useEffect, useState } from 'react'
import { CartContext } from '../Context/CartContext'
import Spinner from '../Spinner/Spinner'
import { Link } from 'react-router-dom'
import { Helmet } from 'react-helmet'

export default function Cart() {
    let {cartList,setCartList,setNumbrsItems,getItemFromCart,deleteItemFromCart,updateCartQuantity}=useContext(CartContext)
    let [loading,setsetLoading]=useState(true)
    async function getCartData(){
        setsetLoading(true)
        let reqGetCart=await getItemFromCart().catch((err)=>{
            if(err.responsse.data.statusMsg==='fail'){
                setCartList([])
                setsetLoading(false)
            }

        })
        if(reqGetCart?.data.status==="success"){
            setCartList(reqGetCart?.data?.data)
            setsetLoading(false)
        }
    }
    async function deleteItemCart(id){
        let reqRemoveItem=await deleteItemFromCart(id)
        if(reqRemoveItem?.data.status==="success"){
            setCartList(reqRemoveItem?.data.data)
            setNumbrsItems(reqRemoveItem.data.numOfCartItems)
        }
        console.log(reqRemoveItem)
    }
    async function updateCart(id,count){
        if(count===0){
            deleteItemCart(id)
        }else{
            let reqUpdate=await updateCartQuantity(id,count).catch((err)=>{console.log(err)})
            if(reqUpdate?.data.status==="success"){
                setCartList(reqUpdate?.data.data)
            }
        }
    }

    useEffect(()=>{
        getCartData()
    },[])

    return <>
    <Helmet>
        <meta charSet="utf-8" />
        <meta name="description" content="Brands Lg Canon Dell Lenovo soney men women " />
        <title>Cart Shopping</title>
    </Helmet>
    {loading?<Spinner/>:<>
    
    {cartList.products.length===0?
    <div className='alert alert-danger my-5'>You have not added any product</div>
    :
    <div className="container py-5">
        <div className='d-flex justify-content-between align-items-center'>
                <h4 className='py-2'>Total Price:<span className='text-main'> {cartList?.totalCartPrice} EGP </span></h4>
                <Link to={'/checkout/'+cartList._id } className='btn bg-btn text-white'>Check Out Payment</Link>
        </div>
        {cartList?.products.map((element)=>{
            return <div className="row py-3 align-items-center border-bottom border-2">
                        <div className="col-md-10 col-10">
                            <div className="row align-items-center">
                                <div className="col-md-2 col-4">
                                    <Link to={"/productDetelis/" + element.product.id}>
                                                <img src={element.product.imageCover} alt="" className='w-100' />
                                    </Link>
                                </div>
                                <div className="col-md-10 col-8">
                                    <h5 className='text-main'>{element.product.title}</h5>
                                    <h6 className='text-muted py-2 '>price:{element.price}EGP</h6>
                                    <button onClick={()=>{deleteItemCart(element.product._id)}} className='btn btn-danger'>Remove <i className='fa-solid fa-trash'></i></button>
                                </div> 
                            </div>
                        </div>
                        <div className="col-md-2 col-2">
                            <span onClick={()=>{updateCart(element.product.id,element.count+=1)}} className='btn btn-success btn-sm' >
                                <i className='fa-solid fa-plus'></i>
                            </span>
                            <span className='mx-2'>
                                {element.count}
                            </span>
                            <span onClick={()=>{updateCart(element.product.id,element.count-=1)}} className='btn btn-danger btn-sm' >
                                <i className='fa-solid fa-minus'></i>
                            </span>
                        </div>
                    </div>
        })}                    
    </div>}
    </>
    
    }
    </>
}
