
import axios from "axios";
import { createContext, useState} from "react";

export let WishListContext=createContext()
export function WishListContextProvider({children}){
    let [numbrsWish,setNumbrsWish]=useState(0)
    let [wishList,setWishList]=useState([])


    function addItemToWishList(id){
        let body={
            productId:id
        }
        let option={
            headers:{
                token:localStorage.getItem('user')
            }
        }
        return axios.post("https://ecommerce.routemisr.com/api/v1/wishlist",body,option)
    }

    function getAllWishCart(){
        let option={
            headers:{
                token:localStorage.getItem('user')
            }
        }
        return axios.get("https://ecommerce.routemisr.com/api/v1/wishlist",option)
    }
    function removeWish(id){
        let option={
            headers:{
                token:localStorage.getItem('user')
            }
        }
        return axios.delete(`https://ecommerce.routemisr.com/api/v1/wishlist/${id}`,option)
    }


    return <WishListContext.Provider value={{removeWish,wishList,setWishList,getAllWishCart,addItemToWishList,setNumbrsWish,numbrsWish}} >
        {children}
    </WishListContext.Provider>
}