/* eslint-disable jsx-a11y/img-redundant-alt */
import React from 'react'
import notFoundImage from '../../asset/images/notFound.png'
import { Helmet } from 'react-helmet'

export default function NotFound() {
  return <>
    <Helmet>
        <meta charSet="utf-8" />
        <meta name="description" content="Brands Lg Canon Dell Lenovo soney men woman Music books" />
        <title>NotFound Page</title>
    </Helmet>
        <div className='d-flex justify-content-center'>
          <img src={notFoundImage} alt='Not Found Image' className='w-25'/>
        </div>
  </>
}
