import React, {useState } from 'react'
import { useFormik } from 'formik'
import * as yup  from 'yup'
import axios from 'axios'
import { useNavigate } from 'react-router-dom'
import styleRest from './ForgetPassword.module.css'

export default function ForgetPassword() {
    let navg=useNavigate()
    let [err,setError]=useState("")
    let [formState,SetFormState]=useState(true)
    let[loading,setLoading]=useState(true)

    // on submit
    // obj in value replace formForget.values
    async function loginApi(value){
    setLoading(false)
        
    let reqForget=await axios.post('https://ecommerce.routemisr.com/api/v1/auth/forgotPasswords',value).catch((erro)=>{
        console.log(erro.response.data.message)
        setError(erro.response.data.message)
        setLoading(true)
        
    })
    if(reqForget.data.statusMsg==='success'){
        SetFormState(false)
    }
    }
    async function restCodeApi(value){
        let reqRest=await axios.post('https://ecommerce.routemisr.com/api/v1/auth/verifyResetCode',value).catch((erro)=>{
            setError(erro.response.data.message)
            
        })
        if(reqRest.data.status==="Success"){
            navg('/restPassword')
        }
    }
    // validation input
    let validate=yup.object({
        email:yup.string().required("please Enter Your Email....").email("Enter Valid Email...."),
    })
    // validation input
    let validate2=yup.object({
        resetCode:yup.string().required("please Enter Your Code....").matches(/^[0-9]{5,6}$/,'Enter Valid Code'),
    })

    // lib form
    let formForget= useFormik({
        initialValues:{
        email:"",
        },
        onSubmit:loginApi,
        validationSchema:validate,
    })
    
    let formRestCode=useFormik({
        initialValues:{
            resetCode:"",
        },
        onSubmit:restCodeApi,
        validationSchema:validate2
    })

    


    return<>
    <div className="container">
        {formState?<>
        {err !== ""?<div className="alert alert-danger mt-3" role="alert">{err}</div>:''}
        
        <form onSubmit={formForget.handleSubmit}>
        <div className='mb-2 mt-2'>
            <label htmlFor="email">Enter Your E-Mail</label>
            <input onBlur={formForget.handleBlur} onChange={formForget.handleChange} value={formForget.values.email} className='form-control mt-2 mb-2' type="email" name="email" id="email" placeholder='Enter Email....' />  
            {formForget.touched.email&&formForget.errors.email?<div className="alert alert-danger" role="alert">{formForget.errors.email}</div>:''}
        </div>
        {loading?<button onClick={formForget.handleSubmit } type="submit" className='btn btnColorBG' disabled={!(formForget.isValid && formForget.dirty)}>Login</button>:<button type='button'  className={styleRest.btnColorBG + ' btn text-white'}><i className='fa-solid fa-spinner fa-spin'></i></button>}

        </form></>
        :<>
        <form onSubmit={formRestCode.handleSubmit}>
            {err !== ""?<div className="alert alert-danger mt-3" role="alert">{err}</div>:''}
            <label htmlFor="Code">Enter Reset Code</label>
            <input value={formRestCode.values.resetCode} onBlur={formRestCode.handleBlur} onChange={formRestCode.handleChange} className='form-control mt-2 mb-2' type="text" name="resetCode" id="Code" placeholder='Enter Reset Code....' />  
            {formRestCode.touched.restCode&&formRestCode.errors.restCode?<div className="alert alert-danger" role="alert">{formRestCode.errors.restCode}</div>:''}
            
            <button type="submit"  className='btn btnColorBG'>Confirm</button>
        </form></>}
        



        
    </div>
    </>
}

