import axios from 'axios';
import React, { useContext } from 'react'
import toast, { Toaster } from 'react-hot-toast';
import { useQuery } from 'react-query';
import { CartContext } from '../Context/CartContext';
import Spinner from '../Spinner/Spinner';
import { Link } from 'react-router-dom';
import { Helmet } from 'react-helmet';

export default function Products() {
    let {addToCart,setNumbrsItems}=useContext(CartContext)
    async function allDataCart(id){
        let req=await addToCart(id).catch((err)=>{
            toast.error('This is an error!');
        })
        if(req.data.status==="success"){
            setNumbrsItems(req.data.numOfCartItems)
            toast.success('Successfully Added!',{
                position:"top-left"
            });
        }
        }
    function getAllProduct(){
        return axios.get('https://ecommerce.routemisr.com/api/v1/products')
    }
    let {isLoading,data}=new useQuery('productApi',getAllProduct)
    return <>
    <Helmet>
        <meta charSet="utf-8" />
        <meta name="description" content="Brands Lg Canon Dell Lenovo soney men woman Music books" />
        <title>All Products</title>
    </Helmet>
    <Toaster/>

    {isLoading?
    <Spinner/>:<>
    <div className="container py-5">
        <div className="row g-3">
            {
            data?.data.data.map((element)=>{
                return <div className="col-md-2 col-sm-3 col-6" key={element.id}>
                            <div className="itemHome">
                            <Link to={"/productDetelis/" + element.id}>
                                <img src={element.imageCover} alt="Product_Image" className='w-100'/>
                                <div className='p-1'>
                                <h6 className='text-main'>{element.category.name}</h6>
                                <h5 className='pb-3'>{element.title.split(" ").slice(0,2).join(" ")}</h5>
                                <div className='d-flex justify-content-between'>
                                    <span>{element.price+'EGP'}</span>
                                    <span><i className='fa-solid fa-star rating-color'></i>{element.ratingsAverage}</span>
                                </div>
                                </div>
                            </Link>
                                <button className=' btn bg-main text-white d-block w-100' onClick={()=>{allDataCart(element.id)}} >Add Product</button>
                            </div>
                        </div>
            })
            }
            
        </div>
    </div>
    </>}
    
    </>
}
