import axios from 'axios'
import React, { useContext, useEffect, useState } from 'react'
import { useQuery } from 'react-query'
import { useParams } from 'react-router-dom'
import Spinner from '../Spinner/Spinner'
import { CartContext } from '../Context/CartContext'
import toast, { Toaster } from 'react-hot-toast'
import { Helmet } from 'react-helmet'
export default function ProductDetelis() {
    
    let {addToCart,setNumbrsItems}=useContext(CartContext)

    async function allDataCart(id){
        let req=await addToCart(id).catch((err)=>{
            toast.error('This is an error!');
        })
        
        if(req.data.status==="success"){
            setNumbrsItems(req.data.numOfCartItems)
            toast.success('Successfully Added!',{
                position:"top-left"
            });
        }
        console.log(req)
        }




    let {id}=useParams()
    let [productId,setProductId]=useState()
    useEffect(()=>{
        setProductId(id)
    },[])
    let {data ,isLoading}=useQuery(['productDeteilsApi',productId],getSpecificProduct)
    
    function getSpecificProduct(query){
        return axios.get(`https://ecommerce.routemisr.com/api/v1/products/${query.queryKey[1]}`)
    }

    function getSrc(e){
        let imgsrc=e.target.getAttribute('src')
        document.getElementById('myImage').setAttribute('src',imgsrc)
    }

    return <>
    <Toaster/>
    {isLoading?<Spinner/>:
    <div className="container py-5">
        <div className="row align-items-center">
            
            <div className="col-md-3">
                <div className="row g-0 align-items-center">
                    <div className="col-md-2 col-1">
                        {data?.data.data.images.map((ele)=>{
                            return <img src={ele} alt="imgSlider" onClick={getSrc} className='w-100 imgs' />
                        })}
                    </div>
                    <Helmet>
                        <meta charSet="utf-8" />
                        <meta name="description" content="Brands Lg Canon Dell Lenovo soney men woman Music books" />
                        <title>{data?.data.data.title}</title>
                    </Helmet>
                    <div className="col-md-10 col-11">
                        <img src={data?.data.data.imageCover} id='myImage' alt="imgSlider" className='w-100' />
                    </div>
                </div>
            </div>

            <div className="col-md-9">
                <div className="product">
                    <h6 className=''>{data?.data.data.title}</h6>
                    <p className='pb-3 text-muted my-3'>{data?.data.data.description}</p>
                    <h6 className='text-main mb-4'>{data?.data.data.category.name}</h6>
                    <div className='d-flex justify-content-between'>
                        <span>{data?.data.data.price +'EGP'}</span>
                        <span><i className='fa-solid fa-star rating-color'></i>{data?.data.data.ratingsAverage}</span>
                    </div>
                    <button className='btn bg-main text-white d-block w-100 my-1' onClick={()=>{allDataCart(data?.data.data.id)}}>Add Product</button>

                </div>

            </div>
        </div>
    </div>}
    
    
    
    </>
}
