import React, { useContext, useEffect} from 'react'
import { Link, NavLink, useNavigate } from 'react-router-dom'
import logo from '../../asset/images/logo market.png (1).png'
import styleNav from './Navbar.module.css'
import { UserContext } from '../Context/UserContext'
import { CartContext } from '../Context/CartContext'
import { WishListContext } from '../Context/WishListContext'


export default function Navbar() {
  let {numbrsItems}=useContext(CartContext)
  let {numbrsWish}=useContext(WishListContext)


  let {userToken,data,setUserToken}=useContext(UserContext)
  let navg=useNavigate()
  
  function logOut(){
    localStorage.removeItem('user')
    setUserToken(null)
    navg('/login')

    
  }
  function navSlidr(){
    let nav=document.querySelector(".navbar")
    let arrow=document.querySelector('.arrow')
    window.addEventListener('scroll',()=>{
      // eslint-disable-next-line no-restricted-globals
      if(scrollY>500){
        nav.classList.add("position-fixed")
        arrow.classList.remove("opacity-0")
      }else{
        nav.classList.remove("position-fixed")
        arrow.classList.add("opacity-0")

      }
      console.log()
      // console.log(scrollY)
    })

  }
  function scrollToTop(){
    window.scrollTo({ top: 0, behavior: 'smooth' });
  }

  useEffect(()=>{
    navSlidr()
  },[])
  return (<>
  <div onClick={scrollToTop} className='arrow position-fixed bottom-0 end-0 p-2 bg-btn me-3 mb-3 rounded-circle z-index opacity-0'><i className='fa-solid fa-arrow-up fa-xl text-white'></i></div>
  <nav className="navbar z-index navbar-expand-lg bg-body-tertiary w-100">
  <div className="container-fluid">
    <Link className="navbar-brand" to='home' ><img src={logo} alt='logo' className={styleNav.logo}/></Link>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      {
      userToken!=null?<ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <NavLink to='home' className={({ isActive}) =>isActive ? "nav-link activeLink" : "nav-link"}>Home</NavLink>
        </li>
        <li className="nav-item">
          <NavLink to='Products' className={({ isActive}) =>isActive ? "nav-link activeLink" : "nav-link"}>Products</NavLink>
        </li>
        <li className="nav-item">
          <NavLink to='category' className={({ isActive}) =>isActive ? "nav-link activeLink" : "nav-link"}>Category</NavLink>
        </li>
        <li className="nav-item">
          <NavLink to='cart' className={({ isActive}) =>isActive ? "nav-link activeLink" : "nav-link"}>Cart</NavLink>
        </li>
        <li className="nav-item">
          <NavLink to='brands' className={({ isActive}) =>isActive ? "nav-link activeLink" : "nav-link"}>Brands</NavLink>
        </li>
      </ul>:''
      }
      <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
        {
          userToken!=null?<>
          <NavLink to='cart' className={({ isActive}) =>isActive ? "activeLink" : ""} >
            <span className="position-relative">
              <i className="fa-solid fa-cart-shopping fs-4 text-main mt-2 cursor-pointer me-2 mb-2"></i><span className="position-absolute bottom-0 mb-2 end-0 translate-middle-y bg-danger rounded-2 px-1 text-white">{numbrsItems}</span>
            </span>
          </NavLink>
          <NavLink to='wish' className={({ isActive}) =>isActive ? "activeLink" : ""} >
            <span className="position-relative">
              <i className="fa-solid fa-heart  fs-4 text-main mt-2 cursor-pointer me-2"></i><span className="position-absolute bottom-0 mb-2 end-0 translate-middle-y bg-danger rounded-2 px-1 text-white">{numbrsWish}</span>
            </span>
          </NavLink>
          
          <li className="nav-item">
            <span className={styleNav.pointer+' nav-link'} onClick={logOut}>Logout</span>
          </li>
          <li className="nav-item">
            <span className='nav-link'>{data?.name +''}</span>
          </li>
            </>:<>
          <li className="nav-item">
            <NavLink to='login' className={({ isActive}) =>isActive ? "nav-link activeLink" : "nav-link"}>Login</NavLink>
          </li>
          <li className="nav-item">
            <NavLink to='' className={({ isActive}) =>isActive ? "nav-link activeLink" : "nav-link"}>SignUp</NavLink>
          </li>
          </>
        }
      </ul>
    </div>
  </div>
</nav>
  
  
  </>)
}
