/* eslint-disable no-restricted-globals */
import React, { useContext, useEffect } from 'react'
import Navbar from '../Navbar/Navbar'
import { Outlet } from 'react-router-dom'
import Footer from '../Footer/Footer'
import { CartContext } from '../Context/CartContext'
import { UserContext } from '../Context/UserContext'
import { WishListContext } from '../Context/WishListContext'
import { Offline } from 'react-detect-offline'

export default function Layout() {
  let {setNumbrsItems,getItemFromCart}=useContext(CartContext)
  let {getAllWishCart,setNumbrsWish}=useContext(WishListContext)
  let {setUserToken}=useContext(UserContext)
  
  useEffect(()=>{
    if(localStorage.getItem('user') != null){
      setUserToken(localStorage.getItem('user'))
      getCartData()
      allWishCart()
    }
  },[])

  async function getCartData(){
    let reqGetCart=await getItemFromCart().catch((err)=>{
      console.log(err)
    })
    if(reqGetCart?.data?.status==='success'){
      setNumbrsItems(reqGetCart.data.numOfCartItems)
    }
  }

  async function allWishCart() {

    let req=await getAllWishCart().catch((err)=>{console.log(err)
    })
    if(req?.data?.status==='success'){
      setNumbrsWish(req.data.count)
    }
}

  return (<>
      
        <Offline>
          <div className='netWork alert alert-danger position-fixed bottom-0 start-0 ms-3 '>
            You Are Offline Now , Check Your Connection
          </div>
        </Offline>
      <Navbar/>
      <Outlet/>
      <Footer/>
    
    </>)
}
