import { useFormik } from 'formik'
import React, { useContext, useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import * as yup  from 'yup'
import { CartContext } from '../Context/CartContext'
import { Helmet } from 'react-helmet'


export default function CheckOut() {
  let[loading,setLoading]=useState(true)
  let {id}=useParams()
  let {checkOutPayment,ClearCart}=useContext(CartContext)



  // validation input
  let validate=yup.object({
    details:yup.string().required("please Enter Your Details....").matches(/^[\w-]{3,}$/,"Enter Valid Details...."),
    phone:yup.string().matches(/^01[0152][0-9]{8}$/,"please Enter valid phone....").required("please Enter Your Phone...."),
    city:yup.string().required("please Enter Your City....").matches(/^[\w-]{3,}$/,"Enter Valid City...."),
  })

    async function paymentCheckOut(value){
    let req=await checkOutPayment(id,value)
    if(req?.data.status==='success'){
      setLoading(false)
      window.open(req.data.session.url,'_self')
    }
    console.log(req)
  }

  let formik=useFormik({
    initialValues:{
        details: "",
        phone: "",
        city: ""
    },
    onSubmit:paymentCheckOut,
    validationSchema:validate
  })

  return <>
    <Helmet>
        <meta charSet="utf-8" />
        <title>Payment</title>
    </Helmet>
  <div className="container">
    <h3 className='mt-5'>Payment Now....</h3>
    <form onSubmit={formik.handleSubmit}>
      <div className='mb-2 mt-2'>
          <label htmlFor="city">City</label>
          <input onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.city} className='form-control mt-2 mb-2' type="text" name="city" id="city" placeholder='Enter City....' />  
          {formik.touched.city&&formik.errors.city?<div className="alert alert-danger" role="alert">{formik.errors.city}</div>:''}
      </div>
      <div className='mb-2 mt-2'>
          <label htmlFor="phone">Phone</label>
          <input onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.phone} className='form-control mt-2 mb-2' type="tel" name="phone" id="phone" placeholder='Enter phone.....' />  
          {formik.touched.phone&&formik.errors.phone?<div className="alert alert-danger" role="alert">{formik.errors.phone}</div>:''}
      </div>
      <div className='mb-2 mt-2'>
          <label htmlFor="details">Details</label>
          <textarea onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.details} className='form-control mt-2 mb-2' name="details" id="details" placeholder='Enter details....' ></textarea>
          {formik.touched.details&&formik.errors.details?<div className="alert alert-danger" role="alert">{formik.errors.details}</div>:''}
      </div>
    {loading?<button onClick={formik.handleSubmit } type="submit" className='btn text-white btnColorBG d-block w-100' disabled={!(formik.isValid && formik.dirty)}>Payment<i className='fa-brands fa-cc-visa ms-2'></i></button>:<button className=' btn bg-btn text-white d-block w-100'><i className='fa-solid fa-spinner fa-spin'></i></button>}
    </form>
  </div>
  </>
  
}