import axios from 'axios'
import React, { useEffect, useState } from 'react'
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';

export default function CategorySlider() {
    let [category,setCategory]=useState([])
    async function getCategory(){
        let reqGetCategory=await axios.get('https://ecommerce.routemisr.com/api/v1/categories')
        setCategory(reqGetCategory.data.data)
    }
    useEffect(()=>{
        getCategory()
    },[])

    return <>
        <div className="container catogreySlider">
            <h4 className='text-main'>Categories</h4>
            <OwlCarousel items={5} loop autoplay autoplayTimeout={2000}>
                {category.map((element)=>{
                    return <div className="item">
                                <img src={element.image} className='max-height'  alt="category" />
                                <p className='fs-14 m-0 text-main text-center'>{element.name}</p>
                            </div>
                })}
            </OwlCarousel>
        </div>
    
    </>
}
