import React, {useContext, useState } from 'react'
import { useFormik } from 'formik'
import * as yup  from 'yup'
import axios from 'axios'
import styleLogin from './Login.module.css'
import {useNavigate } from 'react-router-dom'
import { UserContext } from '../Context/UserContext'
import { CartContext } from '../Context/CartContext'

export default function Login() {
  let {setUserToken}=useContext(UserContext)
  let {setNumbrsItems,getItemFromCart}=useContext(CartContext)
  let navg=useNavigate()
  let [err,setError]=useState("")
  let[loading,setLoading]=useState(true)
  // on submit
  // obj in value replace formLogin.values
  async function loginApi(value){
    setLoading(false)
    let reqLogin=await axios.post('https://ecommerce.routemisr.com/api/v1/auth/signin',value)
    .catch((erro)=>
      {
        setError(erro.response.data.message)
        setLoading(true)
      })
      if(reqLogin.data.message==='success'){
        setLoading(true)
        setUserToken(reqLogin.data.token)
        localStorage.setItem('user',reqLogin.data.token)
        getCartData()
        navg('/home')
      }
  }
  async function getCartData(){
    let reqGetCart=await getItemFromCart().catch((err)=>{
      console.log(err)
    })
    if(reqGetCart?.data?.status==='success'){
      setNumbrsItems(reqGetCart.data.numOfCartItems)
    }
  }
  // validation input
  let validate=yup.object({
    email:yup.string().required("please Enter Your Email....").email("Enter Valid Email...."),
    password:yup.string().matches(/^[A-Z][a-zA-Z0-9!@#$%^-_]{6,16}$/,"please Enter valid Password & Start Uppercase Character....").required("Password Required & Start Uppercase Character........"),
  })


  // lib form
  let formLogin= useFormik({
    initialValues:{
      email:"",
      password:"",
    },
    onSubmit:loginApi,
    validationSchema:validate,
  })




  return<>
  <div className="container">
    <h3 className='mt-5'>Login Now....</h3>
    {err !== ""?<div className="alert alert-danger" role="alert">{err}</div>:''}

    <form onSubmit={formLogin.handleSubmit}>
      <div className='mb-2 mt-2'>
        <label htmlFor="email">E-Mail</label>
        <input onBlur={formLogin.handleBlur} onChange={formLogin.handleChange} value={formLogin.values.email} className='form-control mt-2 mb-2' type="email" name="email" id="email" placeholder='Enter Email....' />  
        {formLogin.touched.email&&formLogin.errors.email?<div className="alert alert-danger" role="alert">{formLogin.errors.email}</div>:''}
      </div>
      <div className='mb-2 mt-2'>
        <label htmlFor="password">Password</label>
        <input  onBlur={formLogin.handleBlur} onChange={formLogin.handleChange} value={formLogin.values.password} className='form-control mt-2 mb-2' type="password" name="password" id="password" placeholder='Enter Password.....' />  
        {formLogin.touched.password&&formLogin.errors.password?<div className="alert alert-danger" role="alert">{formLogin.errors.password}</div>:''}
      </div>
      <button type='button' className={styleLogin.color+' nav-link mb-3'} onClick={()=>{navg('/forgetPassword')}}>Forget Password....?</button>
      {loading?<button onClick={formLogin.handleSubmit } type="submit" className='btn btnColorBG' disabled={!(formLogin.isValid && formLogin.dirty)}>Login</button>:<button type='button' className={styleLogin.btnColorBG + ' btn text-white'}><i className='fa-solid fa-spinner fa-spin'></i></button>}
    </form>
  </div>
  </>
}

