import React from 'react'
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import img1 from '../../asset/images/slider-image-1.jpeg';
import img2 from '../../asset/images/slider-image-2.jpeg';
import img3 from '../../asset/images/slider-image-3.jpeg';
import img5 from '../../asset/images/grocery-banner-2.jpeg';


export default function MainSlider() {
    return <>
    <div className="container">  
        <div className="row g-0">
            <div className="col-md-9 col-9">
                <OwlCarousel items={1} loop autoplay autoplayTimeout={5000}>
                    <div className="item">
                        <img src={img1} alt="imageSlider" height={400} className='w-100'/>
                    </div>
                    <div className="item">
                        <img src={img2} alt="imageSlider" height={400} className='w-100'/>
                    </div>
                    <div className="item">
                        <img src={img5} alt="imageSlider" height={400} className='w-100'/>
                    </div>
                </OwlCarousel>
            </div>
            <div className="col-md-3 col-3">
                <img src={img2} alt="imageSlider" height={200} className='w-100'/>
                <img src={img3} alt="imageSlider" height={200} className='w-100'/>
            </div>
        </div>
    </div>
    
    </>
}
