import React, { useEffect, useState } from 'react'
import Spinner from '../Spinner/Spinner'
import axios from 'axios'
import { Helmet } from 'react-helmet'

export default function Brands() {
    let [categoryList,setCategoryList]=useState([])
    let [loading,setLoading]=useState(true)
    async function getAllBrands(){
        setLoading(true)
        let req=await axios.get('https://ecommerce.routemisr.com/api/v1/brands').catch((err)=>{
            console.log(err)
            setLoading(false)
        })
        if(req.status===200){
            setCategoryList(req.data.data)
            setLoading(false)
        }
    }
    useEffect(()=>{
        getAllBrands()
    },[])
    
    return<>
    <Helmet>
        <meta charSet="utf-8" />
        <meta name="description" content="Brands Lg Canon Dell Lenovo soney" />
        <title>Brands</title>
    </Helmet>
    {loading?<Spinner/>:<div className="container py-5">
                                <div className="row g-4">
                                {
                                    categoryList.map((element)=>{
                                        return <div className="col-md-3 col-6">
                                                    <div className="itemCategory border-1 border-bg-secondary-subtle border ">
                                                                <img src={element.image} alt="" height={150} className='w-100'  />
                                                                <h5 className='p-2 m-0 text-center text-main bg-white'>{element.name}</h5>
                                                    </div>
                                                </div>
                                    })
                                }
                                    
                                </div>
                            </div>
                            }


</>
}
