import React, { useContext, useEffect} from 'react'
import axios from 'axios'
import Spinner from '../Spinner/Spinner'
import { useQuery } from 'react-query'
import { Link } from 'react-router-dom'
import MainSlider from '../MainSlider/MainSlider'
import CategorySlider from '../CategorySlider/CategorySlider'
import { CartContext } from '../Context/CartContext'
import toast, { Toaster } from 'react-hot-toast'
import { WishListContext } from '../Context/WishListContext'
import { Helmet } from 'react-helmet'



export default function Home() {


    let {addToCart,setNumbrsItems}=useContext(CartContext)
    let {addItemToWishList,setNumbrsWish,getAllWishCart,setWishList,wishList}=useContext(WishListContext)

    async function allWishCart() {
        let req=await getAllWishCart().catch((err)=>{console.log(err)})
        if(req?.data?.status==='success'){
            setWishList(req.data.data)
        }
    }
    async function AddwishHeart(id,e) {
      let req=await addItemToWishList(id).catch((err)=>{
        console.log(err)
        toast.error('This is an error!');
      })
      if(req.data.status==='success'){
        e.target.classList.replace('fa-regular','fa-solid')
        toast.success('Successfully Added in Wish List',{
          position:"top-right"
        });
        setNumbrsWish(req.data.data.length)
      }
    }
    async function allDataCart(id){
      let req=await addToCart(id).catch((err)=>{
        toast.error('This is an error!');
      })
      if(req.data.status==="success"){
          setNumbrsItems(req.data.numOfCartItems)
          toast.success('Successfully Added!',{
            position:"top-right"
          });
      }
    }
    function getAllProduct(){
      return axios.get('https://ecommerce.routemisr.com/api/v1/products')
    }
    let {isLoading,data}=new useQuery('productApi',getAllProduct)
    useEffect(()=>{
      allWishCart()
      
    },[])

    
  return<>
  <Helmet>
        <meta charSet="utf-8" />
        <meta name="description" content="AllProducts Brands Lg Canon Dell Lenovo soney men woman Music books" />
        <title>Home</title>
  </Helmet>
  <Toaster/>
  {isLoading?
    <Spinner/>:<>
    <MainSlider/>
    <CategorySlider/>
    <div className="container py-5">
      
      <div className="row g-3">
        {
          data?.data.data.map((element)=>{
            return <div className="col-md-2 col-sm-4 col-6" key={element.id}>
                        <div className="itemHome position-relative">
                          {wishList?.length===0?<i className="heart fa-regular fa-heart fa-2x position-absolute top-0 end-0 p-2 text-main" onClick={(e)=>{AddwishHeart(element.id,e)}}></i>:
                          
                          wishList?.map((ele)=>{
                            if(ele.id===element.id){
                              return <i className="heart fa-solid fa-heart fa-2x position-absolute top-0 end-0 p-2 text-main" onClick={(e)=>{AddwishHeart(element.id,e)}}></i>
                            }
                            return <i className="heart fa-regular fa-heart fa-2x position-absolute top-0 end-0 p-2 text-main" onClick={(e)=>{AddwishHeart(element.id,e)}}></i>
                          })
                          }
                          <Link to={"/productDetelis/" + element.id}>
                            <img src={element.imageCover} alt="Product_Image" className='w-100'/>
                            <div className='p-1'>
                              <h6 className='text-main'>{element.category.name}</h6>
                              <h5 className='pb-3'>{element.title.split(" ").slice(0,2).join(" ")}</h5>
                              <div className='d-flex justify-content-between'>
                                <span>{element.price+'EGP'}</span>
                                <span><i className='fa-solid fa-star rating-color'></i>{element.ratingsAverage}</span>
                              </div>
                            </div>
                          </Link>
                            <button className=' btn bg-main text-white d-block w-100' onClick={()=>{allDataCart(element.id)}} >Add Product</button>
                        </div>
                    </div>
          })
        }
        
      </div>
    </div>
    </>}
  </>
}
