import React, { useContext, useEffect, useState } from 'react'
import { WishListContext } from '../Context/WishListContext'
import { Link } from 'react-router-dom'
import Spinner from '../Spinner/Spinner'
import { CartContext } from '../Context/CartContext'
import toast, { Toaster } from 'react-hot-toast'
import { Helmet } from 'react-helmet'

export default function WishCart() {
    let [loading,setLoading]=useState(true)
    let {addToCart,setNumbrsItems}=useContext(CartContext)
    let [x,setx]=useState([])
    
    let {getAllWishCart,wishList,setWishList,removeWish,setNumbrsWish}=useContext(WishListContext)
    async function allWishCart() {
        setLoading(true)
        let req=await getAllWishCart().catch((err)=>{console.log(err)
            setLoading(false)
        })
        if(req?.data?.status==='success'){
            setWishList(req.data)
            setLoading(false)
        }
    }

    async function allDataCart(id){
            let req=await addToCart(id).catch((err)=>{
            toast.error('This is an error!');
            })
            if(req.data.status==="success"){
                setNumbrsItems(req.data.numOfCartItems)
                toast.success('Successfully Added!',{
                position:"top-right"
                });
            }
        }

    async function deleteWishCart(id){
        let req=await removeWish(id).catch((err)=>{console.log(err)})
        
        if(req?.data?.status==='success'){
            setx(req.data.data)
            setNumbrsWish(req.data.data.length)
        }
        console.log(req);
        
    }
    
    useEffect(()=>{
        allWishCart()
    },[])

    useEffect(()=>{
        allWishCart()
    },[x])
    useEffect(()=>{
        return()=>{
            window.location.reload()
        }
    },[])
    return <>
    <Toaster/>
    <Helmet>
        <meta charSet="utf-8" />
        <meta name="description" content="Brands Lg Canon Dell Lenovo soney men woman Music books" />
        <title>Favourite </title>
    </Helmet>
    {loading?<Spinner/>:wishList.data.length===0?
    <div className='alert alert-danger my-5'>You have not added any Wish Product</div>
    :
    <>
        <div className="container py-5">
        {wishList?.data?.map((element)=>{
                return <div className="row py-3 align-items-center border-bottom border-2">
                <div className="col-md-10 col-8">
                    <div className="row align-items-center">
                        <div className="col-md-2 col-6">
                            <Link to={"/productDetelis/" + element.id}>
                                        <img src={element.imageCover} alt="" className='w-100' />
                            </Link>
                        </div>
                        <div className="col-md-10 col-6">
                            <h5 className='text-main fs-6'>{element.title}</h5>
                            <h6 className='text-muted py-2 '>price:{element.price}EGP</h6>
                            <button onClick={()=>{deleteWishCart(element.id)}} className='btn btn-danger'><i className='fa-solid fa-trash'></i></button>
                        </div> 
                    </div>
                </div>
                <div className="col-md-2 col-4">
                    <button className='btn bg-btn text-white' onClick={()=>{allDataCart(element.id)}}>Add To Cart</button>
                </div>
            </div>
        })}
    </div>
    
    
    </>
    }
    
    
    
    </>
}
